:root {
  font-size: 100%;
  /*
  Set the box-sizing property to border-box so that the padding 
  and border are included in the total width and height of the elements.
  https://developer.mozilla.org/en-US/docs/Web/CSS/box-sizing
  */
  box-sizing: border-box;
}

/*
the *, *::before, *::after { box-sizing: inherit; } 
rule sets every element on the page and their ::before and ::after pseudo-elements 
to inherit the box-sizing property from their parent element. 
This is often done at the root level (often with html { box-sizing: border-box; }) 
to establish a consistent box-sizing method across the entire page
*/
*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-inter);
  margin: 0;
  padding: 0;
  background: var(--dark-bg);
  color: var(--dark-white-text);
}

/*
breakpoint.scss is a partial file that is imported into index.scss 
and is used to create the breakpoints for the media queries. 
It allows for a responsive design.
*/
/* 
   Removes top margin from H1, H2, H3 tags.
   This can help with better vertical spacing and alignment of heading tags.
*/
h1, h2, h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

/* 
   Removes text decoration (underline) from all states of anchor tags.
   This can help make links align better with the overall design aesthetics.
*/
a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --font-inter: "Inter", sans-serif;
}

/*
We use :root to set the colors as CSS variables.

The CSS :root pseudo-class selector is used to select the highest-level parent 
of a given specification. In the HTML specification, the :root is essentially equivalent 
to the html selector.

:root {
  background-color: gray;
}

html {
  background-color: gray;
}

*/
:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-1: hsl(37, 97%, 70%);
  --instagram-2: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle: linear-gradient(to right, hsl(210, 78%, 56%), hsl(146, 68%, 55%));
  --toggle-bg: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-blue-text: hsl(228, 34%, 66%);
  --dark-white-text: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(227, 47%, 96%);
  --light-blue-text: hsl(228, 12%, 44%);
  --light-dark-blue-text: hsl(230, 17%, 14%);
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 2.375rem;
  }
}
.header__followers {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-blue-text);
}

/*
Imports all members (variables, functions, mixins) from the '../util' module.
This allows us to use those members in this stylesheet.
*/
.toggle {
  border: none;
}
.toggle__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--toggle-bg);
}
/*
Imports all members (variables, functions, mixins) from the '../util' module.
This allows us to use those members in this stylesheet.
*/
.card {
  padding: 1.25rem;
}